import React from "react";
import ImageFadeIn from "react-image-fade-in";
import Skelet from "../components/fotoSkelet";
import Layout from "../components/layout";
import Kubik from "../../static/images/kubik.jpg";
import { Helmet } from "react-helmet";

const Uvod = () => {
  const image = (props) => (
    <a
      href="https://www.instagram.com/jakub.psenda/"
      target="_blank"
      rel="noreferrer"
      aria-label={`Instagram`}
      className="ig-href"
    >
      <ImageFadeIn
        style={{ width: "100%" }}
        opacityTransition={1.5}
        src={props.src}
        alt={props.alt}
      />
    </a>
  );

  return (
    <Layout>
      <Helmet>
        <title>Úvod | Jakub pšenička | Fotograf</title>
        <meta
          name="description"
          content="Zabývám se tvorbou videii a focením všech možných událostí. Na mých stránkách si můžete prohlednou ukázku mé tvorby, přečíst reference či mě rovnou kontaktovat."
        ></meta>
      </Helmet>
      <main>
        <div id="svatby_header text-secondary">
          <div className="container">
            <div className="w-100">
              <h1 className="text-center mt-5">
                Vítejte na mém fotografickém portfoliu!
              </h1>
              <hr className="hr-thickness" />
            </div>

            <Skelet photoList={photoList} />

            <div className="row mt-5 py-5 d-flex align-items-center">
              <div className="col-12 col-lg-5 px-0">
                <ImageFadeIn
                  opacityTransition={1.5}
                  className="w-100"
                  src={Kubik}
                  alt="jakub psenicka - fotograf - moje fotka"
                />
              </div>

              <div
                className="col-12 col-lg-7 py-0 py-lg-3 px-0"
                style={{ backgroundColor: "#FBFAF7" }}
              >
                <div
                  className=" mx-3 ps-3 py-3 "
                  style={{
                    fontSize: "125%",
                    borderLeft: "0.5px solid #292b2c",
                  }}
                >
                  <p>
                    <b>
                      Ukázku mé práce už jste viděli a nyní vám řeknu pár vět o
                      sobě.
                    </b>
                  </p>
                  <p>Jmenuju se Jakub a jsem především programátor.</p>
                  <p>
                    Nejdříve jsem vystudoval Gympl a následně jsem pak
                    pokračoval na ČVUT, postupně začínal pracovat jako
                    programátor. Nicméně i přes moji hlavní vášeň k počítačům
                    jsem měl blízko i k focení. Jíž na střední škole jsem s
                    kamarádem začal fotit a vypomáhal jsem mu poté i na svatbách
                    nebo maturitních plesech. Ve volném čase mě focení vždy také
                    bavilo a rád si od počítače oddechnu a vyrazím do přírody s
                    foťákem v ruce. Věřím, že práce s foťákem a kamerou je pro
                    mě ideální kombinace a rád se věnuji oběma oborům.
                  </p>
                  <p>
                    Když zrovna nesedím u počítače, tak rád cestuji a poznávám
                    nová místa. Také si ale rád odpočinu na rybách nebo zajdu do
                    fitka.
                  </p>
                  <p>
                    Jestli se vám má práce líbí, napište mi přes kontaktní
                    formulář, nebo na sociálních sítích Instagram a Facebook.
                    Rád se stanu Vaším fotografem/kameramanem.
                  </p>
                </div>
              </div>
              <div className="w-100 mt-0 mt-md-5 py-0 py-md-5 m-0 text-center">
                <div
                  className="row w-100 pb-0 pb-md-0 mt-4 mx-0"
                  style={{
                    fontSize: "120%",
                    borderBottom: "0.003em solid #212529",
                  }}
                >
                  <div className="col-12 col-lg-4 my-2 my-lg-0">
                    <p style={{ fontSize: "120%" }}>Napište mi</p>
                    <p>jakub.psenda@gmail.com</p>
                  </div>
                  <div className="col-12 col-lg-4 ">
                    <p style={{ fontSize: "120%" }}>Zavolejte mi</p>
                    <p>+420 606 071 865</p>
                  </div>
                  <div className="col-12 col-lg-4 my-2 my-lg-0">
                    <p style={{ fontSize: "120%" }}>Facebook</p>
                    <p>Jakub Pšenda</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="mx-0 mx-lg-5 px-0 px-xl-5 instagram-follow">
            <div className="mb-5 mx-0 mx-lx-5 px-0  px-xl-5">
              <p
                style={{ fontSize: "140%" }}
                className="text-center mb-5 text-uppercase bold-text"
              >
                Sledujte moji tvorbu také na instagramu!
              </p>
              <div className="row my-3 mx-auto px-auto align-items-start align-items-md-center">
                {image(ig[0])}
                {image(ig[1])}
                {image(ig[2])}
                {image(ig[3])}
                {image(ig[4])}
              </div>
            </div>
          </div>
        </div>
      </main>
    </Layout>
  );
};

